/* eslint-disable jsx-a11y/alt-text */
import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {
	Collapse,
	Navbar,
	NavbarToggler,
	NavbarBrand,
	Nav,
	// NavItem,
	NavLink,
	UncontrolledDropdown,
	DropdownToggle,
	DropdownMenu,
	DropdownItem,
	NavbarText,
	Button
} from "reactstrap";
import { logoutAction } from "../redux/actions";
import "./navbar.css";
import cartImage from "../image/cart.png";
import logoImage from "../image/logo.jpg";

class NavBar extends Component {
	state = { isOpen: false };

	toggle = () => {
		this.setState({ isOpen: !this.state.isOpen });
	};
	logout = () => {
		const { logoutAction } = this.props;
		logoutAction();
		localStorage.removeItem("id");
	};
	renderNavBarLoggedIn = () => {
		const { userID } = this.props;
		if (userID !== 0) {
			return (
				<DropdownMenu right>
					<Link to="/">
						<DropdownItem onClick={this.logout}>Logout</DropdownItem>
					</Link>
					<Link to="/cart" className="notification">
						<DropdownItem>Cart </DropdownItem>
						<span className="badge">{this.props.cart.length}</span>
					</Link>

					<DropdownItem divider />
					<DropdownItem>Reset</DropdownItem>
				</DropdownMenu>
			);
			// }
		} else {
			return (
				<DropdownMenu right>
					<Link to="/login">
						<DropdownItem>Login</DropdownItem>
					</Link>
					<DropdownItem>Option 2</DropdownItem>
					<DropdownItem divider />
					<DropdownItem>Reset</DropdownItem>
				</DropdownMenu>
			);
		}
	};

	render() {
		const { userEmail } = this.props;
		return (
			<div>
				<Navbar 
          			style={{backgroundColor: "black", color: "white"}}
          			dark
          			expand="md"				
				>	
					<Link to="/">
						<NavbarBrand >
              			<img style={{
              			  width:"50px"
              			}} src={logoImage} />						
						</NavbarBrand>
					</Link>
					<NavbarToggler onClick={this.toggle} />
					<Collapse isOpen={this.state.isOpen} navbar>
						<Nav className="mr-auto" navbar>
							<UncontrolledDropdown nav inNavbar>
								<DropdownToggle nav caret>
									Options
								</DropdownToggle>
								{this.renderNavBarLoggedIn()}
							</UncontrolledDropdown>
						</Nav>
            {userEmail ? (
              <div>
              <Link to="/cart">
              <NavbarBrand>
                <NavLink >History</NavLink>
              </NavbarBrand>
              </Link>
{/* <Link to="/cart" className="notification">
<DropdownItem>Cart </DropdownItem>
<span className="badge">{this.props.cart.length}</span>
</Link> */}
              <Link to="./cart" >
              <NavbarBrand  style={{backgroundColor:"black"}} className="notification"> 
              <img src={cartImage} style={{width:"50px"}} /> 
			  <span className="badge"> {this.props.cart.length} </span>
			
              </NavbarBrand>
              </Link>
              <NavbarText style={{marginRight:"10px"}}> <h6>{userEmail}</h6></NavbarText>
              <Link to="/">
              <Button outline color="secondary" onClick={this.logOut}>Logout</Button>
              </Link>
              </div>
            ) : null}
              {userEmail ? (
                null
            ) : (
              <Link to="/login">
              <Button outline color="secondary">Login</Button>
              </Link>
            ) }						

						{/* <NavbarText>{userEmail !== "" ? userEmail : ""}</NavbarText> */}
					</Collapse>
				</Navbar>
			</div>
		);
	}
}
const mapStatetoProps = (state) => {
	return {
		userID: state.user.id,
		userRole: state.user.role,
		userEmail: state.user.email,
		cart: state.cart.cart,
	};
};

export default connect(mapStatetoProps, { logoutAction })(NavBar);
