import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, Table } from "reactstrap";
import { fetchCartAction } from "../redux/actions";

class CartPage extends Component {
	state = {};
	componentDidMount() {
		const { userID, fetchCartAction } = this.props;
		fetchCartAction(userID);
	}

	renderCart = () => {
		const { cart } = this.props;
		return cart.map((val) => {
			return (
				<tr>
					<td>{val.categoryID}</td>
					<td>{val.name}</td>
					<td>
						<img src={val.image} alt="" height="50px" />
					</td>

					<td>
						<Button>-</Button>
					</td>
					<td>{val.qty}</td>
					<td>
						<Button>+</Button>
					</td>
					<td>{val.qty * val.price}</td>
					<td>
						<Button>delete</Button>
					</td>
				</tr>
			);
		});
	};
	render() {
		return (
			<div>
				<Table style={{ textAlign: "center" }}>
					<thead>
						<tr>
							<th>Category ID</th>
							<th>name</th>
							<th>image</th>
							<th colspan="3">qty</th>
							<th>price</th>
							<th>action</th>
						</tr>
					</thead>
					<tbody>{this.renderCart()}</tbody>
				</Table>
			</div>
		);
	}
}
const mapStatetoProps = (state) => {
	return {
		cart: state.cart.cart,
		userID: state.user.id,
	};
};
export default connect(mapStatetoProps, { fetchCartAction })(CartPage);
